/* HERO */
.hero {
  height: 95vh;
  background-color: var(--color-primary);
  width: 100%;
  clip-path: polygon(0 0, 100% 0, 100% 100%, 0 87%);

  display: grid;
  grid-template-rows: repeat(3, 1fr);
  grid-template-columns: repeat(3, 1fr);
  position: relative;
  z-index: 10;
}

.hero__text {
  grid-row: 2;
  grid-column: 2;

  display: grid;
  grid-template-rows: 2;
}

.hero__title {
  font-size: 10rem;
  color: var(--color-secondary);
  line-height: 1;

  align-self: flex-end;
  justify-self: center;
}

.hero__tag {
  font-size: 4rem;
  color: var(--color-white);

  align-self: flex-start;
  justify-self: end;
}

/* FEATURES */
.features {
  margin: 2rem;
}

@media only screen and (max-width: 87.5em) {
  .features {
    margin: 0;
  }
}

.features__title {
  font-size: 5rem;
  text-align: center;
  color: var(--color-secondary);
}

.features__container {
  display: grid;
  margin: 5rem 20rem;
  grid-template-columns: repeat(auto-fit, minmax(25rem, 1fr));
  grid-gap: 8rem;
}

@media only screen and (max-width: 87.5em) {
  .features__container {
    margin: 5rem;
  }
}

.card--1 {
  grid-column: 1 / 1;
}

.card--1 {
  grid-column: 2 / 3;
}

.card--1 {
  grid-column: 3 / -1;
}

/* FOOTER */

.footer {
  height: 25vh;
  background-color: var(--color-primary);

  display: flex;
  justify-content: center;
  align-items: center;
}

@media only screen and (max-width: 28.125em) {
  footer {
    padding: 10rem;
  }
}

.footer__text {
  font-size: 1.6rem;
  font-weight: 600;
  color: var(--color-tertiary);
}

.footer__text a {
  color: var(--color-secondary);
  text-decoration: none;
}
