.table-class {
  font-size: 2rem;
  border: 1px solid #ccc;
  border-radius: 5px;
  border-collapse: collapse;
  margin: 1rem;
}

.table-class .th-class {
  color: var(--color-primary);
  background-color: var(--color-tertiary);
  border-bottom: 1px solid #ccc;
  border-left: 1px solid #ccc;
}

.table-class .th-class button {
  padding: 0.5rem;
  margin-bottom: 1rem;
}

.table-class .td-class {
  text-align: center;
  border-width: 1px 1px 1px 1px;
  border-style: solid;
  border-color: #ccc;
  padding: 5px;
}

.table-class tbody tr:nth-child(even) {
  background-color: var(--color-white);
}

.table-class tbody button {
  border: none;
  padding: 1rem 2rem;
}

.table-class tbody .Update {
  color: var(--color-white);
  background-color: var(--color-primary);
  filter: brightness(0.9);
}

.table-class tbody .Update:hover {
  filter: brightness(1);
}

.table-class tbody .Delete {
  color: var(--color-white);
  background-color: var(--color-secondary);
  filter: brightness(0.9);
}

.table-class tbody .Delete:hover {
  filter: brightness(1);
}

/* .table-class tbody tr td:last-child {
  width: 1%;
  white-space: nowrap;
} */

.search-class {
  padding: 2px;
  margin: 10px;
}

.search-class input {
  font-size: 2.5rem;
  padding: 10px;
}

.pagination-class {
  font-size: 2.5rem;
  padding: 5px;
  margin: 10px;

  display: flex;
  align-items: center;
  justify-content: space-between;
}

.pagination-class button {
  font-size: 2rem;
  padding: 7px 10px;
  margin-left: 1rem;
}

@media only screen and (max-width: 52em) {
  .table-class tbody {
    display: inline-block;
  }

  .table-class thead {
    display: none;
  }

  .table-class button {
    width: 100%;
  }

  .search-class input {
    font-size: 2.5rem;
  }

  .pagination-class {
    font-size: 2rem;
  }

  .pagination-class button {
    padding: 3px 5px;
  }
}
