/*

#0b3c5d
#ff533d
#f4decb
#f8eee7

*/

:root {
  --color-primary: #0b3c5d;
  --color-secondary: #ff533d;
  --color-tertiary: #f4decb;
  --color-white: #f8eee7;
}

* {
  margin: 0;
  padding: 0;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-size: 62.5%; /* 1 rem = 10px */
}

@media only screen and (max-width: 62.5em) {
  html {
    font-size: 50%;
  }
}

@media only screen and (max-width: 28.125em) {
  html {
    font-size: 40%;
  }
}

body {
  font-family: 'Source Sans Pro', sans-serif;
  font-weight: 300;
  font-size: 1.2rem;
  line-height: 1.6;
  color: #0b3c5d;
  background-color: var(--color-tertiary);
  min-height: 100vh;
  margin: 3rem;
}

@media only screen and (max-width: 75em) {
  body {
    margin: 0;
  }
}
