.previewitor-container {
  font-size: 2rem;
  font-weight: 600;
  background-color: white;
  margin: 1rem;
  padding: 1rem 5rem;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

.previewitor-container form {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(25rem, 1fr));
  grid-gap: 3rem;

  justify-items: center;
  align-items: center;
}

.previewitor-container label {
  display: block;
}

.previewitor-container input {
  font-size: inherit;
}

.previewitor-container textarea {
  font-size: inherit;
}

.previewitor-container button {
  padding: 1rem 3rem;
  margin-right: 2rem;
  border: none;
}

.form__buttons {
  align-self: flex-end;
}

.form__buttons--do {
  background-color: var(--color-primary);
  color: var(--color-white);
  filter: brightness(0.9);
}

.form__buttons--do:hover {
  filter: brightness(1);
}

.form__buttons--cancel {
  background-color: var(--color-secondary);
  color: var(--color-white);
  filter: brightness(0.9);
}

.form__buttons--cancel:hover {
  filter: brightness(1);
}

@media only screen and (max-width: 62.5em) {
  .previewitor-container {
    margin: 0;
  }
}
