.inventory__back {
  margin-left: 1rem;
  margin-bottom: 2rem;
}

.inventory__link {
  color: var(--color-secondary);
  font-size: 1.5rem;
  font-weight: 700;
}

.grid-container {
  background-color: white;
  color: var(--color-primary);
  margin: 1rem;
  margin-top: 0;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  overflow-x: auto;
}

.button-container {
  display: flex;
  border: 1px solid black;
  margin: 1rem;
  margin-bottom: 0;
  justify-content: space-evenly;
}

.newButton {
  display: inline-block;
  padding: 10px 30px;
  border: none;
  background-color: var(--color-secondary);
  filter: brightness(0.9);
  color: var(--color-white);
  font-size: large;
  width: 100%;
  transition: all 0.2s;
}

.newButton:hover {
  filter: brightness(1);
}

.contextSwitcher {
  display: inline-block;
  padding: 10px 10px;
  border: none;
  background-color: var(--color-primary);
  filter: brightness(0.95);
  color: white;
  font-size: large;
  width: 100%;
  transition: all 0.2s;
}

.contextSwitcher:hover {
  filter: brightness(1.2);
}

@media only screen and (max-width: 62.5em) {
  .button-container {
    margin: 0;
    flex: 1 1 auto;
  }

  .grid-container {
    margin: 0;
  }
}
